class Card {
    constructor(body, img, price) {
        this.body = body;
        this.img = img;
        this.price = price;
        this.id = Math.random();
    }
}
class ServiceCard extends Card {
    constructor(body, img, descr) {
        super(body, img);
        this.descr = descr;
    }
}


