import { useEffect } from 'react';
import { useState } from 'react';
import Sales from '../img//Sales.webp';
import { setCurrentTimer } from './Help/TimerHelper';

const defaultRemainingTime = {
    d: '00',
    h: '00',
    m: '00',
    s: '00',
}

export default function SaleTimer() {

    const [{d, h, m, s}, setRemainingTime] = useState(defaultRemainingTime);
    // const [date, setDate] = useState(new Date("8/13/2022 20:00:00").valueOf());
    // console.log((date - new Date().valueOf()) <= 0);

    const updateTimer = (countdown) => setRemainingTime(setCurrentTimer(countdown));

    useEffect(() => {
        const intervalId = setInterval(() => {
            updateTimer(new Date("2/10/2023 24:00:00").valueOf())
        }, 1000);
        
        // (function() {
        //     if ((date - new Date().valueOf()) <= 0) {
        //         setDate(new Date().valueOf() + 432000000);
        //     }
        // })()
        
        return () => clearInterval(intervalId);
    }, [])

    return (
        <section className='sales'>
            <h3 className='container'></h3>
            <div className="sales_offer container">
                <img style={{borderRadius: '15%'}} src={Sales} alt="sale" />
                <ul className="collection">
                    <li className="collection-item">       10-15%</li>
                    <li className="collection-item">   10%</li>
                    <li className="collection-item">    7%</li>
                </ul>
            </div>
                
            <h4>  ...</h4>
            <div className='sales_timer container'>
                <div className='sales_timer_item'>
                    <span>{d}</span>
                    <p></p>
                </div>
                <div className='sales_timer_item'>
                    <span>{h}</span> 
                    <p></p> 
                </div>
                <div className='sales_timer_item'>
                    <span>{m}</span> 
                    <p></p> 
                </div>
                <div className='sales_timer_item'>
                    <span>{s}</span> 
                    <p></p> 
                </div>
            </div>

                <div className="b_y_block b_y_block_call">
                    <div>
                              . <br />
                         -   ! 
                    </div>
                    <button className="my_btn pulsate"><a href="tel:89373146400"></a></button>
                </div>
        </section>
     )
}